<template>
  <div
    class="home"
    :class="{ min: screenWidth < 700, med: screenWidth < 1200 }"
  >
    <!-- 页头部分 -->
    <div class="header">
      <div class="title">
        <span class="name">次顶级</span>
        <p>
          <span class="describe">了解真相，才能获得真正的自由！</span>
          <span class="webUrl" @click="copyUrL">复制地址：https://subtoph.github.io</span>
        </p>
      </div>
    </div>
    <!-- 底部内容部分，包括导航和内容 -->
    <div class="contentBox">
      <!-- 导航盒子 -->
      <div class="navBox">
        <!-- 导航盒子布局 -->
        <div class="navBox_border" :class="{ clearPadding: !switchNavBox }">
          <!-- 导航组件 -->
          <my-transition>
            <MyNav
              v-show="switchNavBox"
              :actNav="actNav"
              :navList="navList"
              @changeNavAct="changeNavAct"
            ></MyNav>
          </my-transition>
          <!-- 切换整个导航显示隐藏按键 -->
          <div class="navBut" @click="switchNavBox = !switchNavBox">
            {{ switchNavBox ? "收起导航" : "展开导航" }}
          </div>
        </div>
      </div>
      <!-- 内容部分 -->
      <div class="content">
        <router-view />
      </div>
    </div>
  </div>
</template>
<script>
// 自定义过度组件
import myTransition from "../utils/donghua.js";
// 导航组件
import MyNav from "../components/myNav.vue";
export default {
  name: "Home",
  components: {
    MyNav,
    "my-transition": myTransition,
  },
  data() {
    return {
      screenWidth: "", //页面宽度
      switchNavBox: true, //收起展开控制
      actNav: "学习笔记", //当前选中的导航
      navList: [
        //多层级导航数据
        {
          name: "学习笔记",
          index: "notes",
        },
        {
          name: "个人简介",
          index: "about",
        },
      ],
    };
  },
  created() {},
  mounted() {
    this.monitorWidth(); //监听页面宽度
  },

  methods: {
    // 监听页面宽度
    monitorWidth() {
      //初始化页面宽度
      this.screenWidth = document.body.clientWidth;
      window.addEventListener("resize", () => {
        //页面宽度变化继续监听
        this.screenWidth = document.body.clientWidth;
      });
    },
    // 导航变化
    changeNavAct(name, index) {
      this.actNav = name; //当前选中页面
      this.$router.push({ path: index }); //跳转对应页面
    },
    //复制网站地址
    copyUrL(){
      // 创建一个input元素
      const input = document.createElement('input')
      // 添加到body中
      document.body.appendChild(input)
      // 设置input的值
      input.setAttribute('value',"https://subtoph.github.io")
      // 选择文本域内容
      input.select()
      // 执行浏览器复制命令
      // 复制命令会将当前选中的内容复制到剪切板中（这里就是创建的input标签）
      // Input要在正常的编辑状态下原生复制方法才会生效
      if (document.execCommand('copy')) {
        document.execCommand('copy')
      }
      alert('复制成功')   //提示
      // 清除元素
      document.body.removeChild(input)
    }
  },
};
</script>
<style lang="less" scoped>
.home {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(233, 233, 233);
  .header {
    position: fixed;
    top: 0;
    left: 0;
    height: 40px;
    background: #fff;
    width: 100%;
    z-index: 99;
    .title {
      width: 80%;
      line-height: 40px;
      text-align: left;
      margin: 0 auto;
      padding: 0 20px;
      box-sizing: border-box;
      overflow: hidden;
      span{
        display: block;
        float: left;
      }
      p{
        margin: 0;
        float: left;
        margin-left: 30px;
        span{
          float: none;
          line-height: 20px;
          color: #aaa;
        }
      }
      .name {
        font-weight: 700;
        font-size: 24px;
        font-style: italic;
      }
      
    }
  }
  

  .contentBox {
    position: absolute;
    background: rgb(40, 140, 255);
    height: 100%;
    width: 100%;
    padding: 0 200px;
    padding-top: 50px;
    padding-bottom: 10px;
    display: flex;
    box-sizing: border-box;
    transition: 0.3s;
    .navBox {
      margin-right: 10px;
      transition: 0.5s;
      width: 130px;
      .navBox_border {
        position: relative;
        border-radius: 5px;
        background: #fff;
        box-shadow: 0px 2px 2px rgb(117, 117, 117);
        padding: 10px 0;
        &.clearPadding {
          padding: 0;
        }
        .navBut {
          text-align: center;
          cursor: pointer;
          line-height: 40px;
          color: rgb(122, 122, 122);
          &:hover {
            color: rgb(0, 140, 255);
            background: rgb(213, 235, 255);
            border-radius: 5px;
          }
        }
      }
    }
    .content {
      position: relative;
      flex: 1;
      min-width: 700px;
      background: #fff;
      border-radius: 5px;
      padding: 10px;
    }
  }
}

// 名字
.home.min .header .name{
  font-size: 16px;
}
// 标题整体
.home.min .header .title{
  padding: 0 5px;
}
// 内容盒子
.home.med .contentBox{
  padding: 0 10px;
  padding-top: 50px;
}
.home.med .contentBox .navBox{
  min-width: 80px;
  font-size: 12px;
}
.home.med .header .title{
  width: 90%;
}
.home.min .header .title{
  width: 100%;
}
 
</style>
